import React, { useReducer } from "react"
import { useState, useEffect } from "react"
import firebase from "../firebase"
import { Grid, makeStyles, Typography, Container } from "@material-ui/core"
import StackGrid from "react-stack-grid"
import LinkCard from "../components/LinkCards"

import FormDialog from "../components/FormDialog"
import EditFormDialog from "../components/EditFormDialog"
import Notifier from "../components/Snackbar"

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
		width: `calc(100vw - ${theme.spacing(2)}px)`,
    margin: theme.spacing(9,1,18,1)
  },
  folderNameHeader: {
    background: theme.palette.background.paper,
    position: "sticky",
    top: theme.spacing(7),
    zIndex: 5,
    paddingTop: theme.spacing(2)
  },
  snackBar: {
		position: "fixed",
		bottom: theme.spacing(18)
	}
}))

export const ACTIONS = {
  ADD_LINK: "add-link",
  COPY_LINK: "copy-link",
  EDIT_LINK: "edit-link",
  DELETE_LINK: "delete-link",
  UNDO_DELETE: "undo-delete-link",
  SHOW_SNACKBAR: "show-snackbar",
  CLOSE_SNACKBAR: "close-snackbar",
  CLOSE_EDIT_FORM: "close-edit-form"
}

export const CARD_TYPE = {
  REGULAR: "regular-link-card",
  DISCOVER: "discover-link-card"
}

const linkCardsReducer = (state, action) => {
  switch (action.type) {
    case ACTIONS.ADD_LINK: {
      return {
        ...state,
        snackbarUndoButton: false,
        openSnackbar: true,
        snackbarMessage: action.payload.message,
        showForm: false
      }
    }
    case ACTIONS.COPY_LINK: {
      return {
        ...state,
        snackbarUndoButton: false,
        openSnackbar: true,
        snackbarMessage: action.payload.message
      }
    }
    case ACTIONS.EDIT_LINK: {
      return {
        ...state,
        showForm: true,
        clickedLinkData: action.payload.data
      }
    }
    case ACTIONS.DELETE_LINK: {
      firebase.firestore()
        .collection("links")
        .doc(action.payload.id)
        .delete()
        .then(() => {
          console.log("Success")
        }).catch((err) => {
          console.error("Error removing document: ", err)
        })
      
      return {
        snackbarMessage: "Link successfully deleted",
        openSnackbar: true,
        showForm: false,
        clickedLinkData: action.payload.data,
        snackbarUndoButton: true
      }
    }
    case ACTIONS.UNDO_DELETE: {
      firebase.firestore()
        .collection("links")
        .add(state.clickedLinkData)
        .then(() => console.log("Link delete undone"))
        .catch(err => console.error("Error encountered: ", err))
      
      return {
        ...state,
        openSnackbar: true,
        snackbarUndoButton: false,
        snackbarMessage: "Link restored"
      }
    }
    case ACTIONS.CLOSE_SNACKBAR: {
      return {
        ...state,
        openSnackbar: false
      }
    }
    case ACTIONS.CLOSE_EDIT_FORM: {
      return {
        ...state,
        showForm: false
      }
    }
    default: return state
  }
}

const initialState = {
  openSnackbar: false,
  showForm: false,
  snackbarMessage: "",
  snackbarUndoButton: false,
  clickedLinkData: {}
}

const Folder = ({ id, name, ...props}) => {

  const classes = useStyles()
  const [state, dispatch] = useReducer(linkCardsReducer, initialState)

  const linkData = useDetails(id, props.fetchAll)
  const links = linkData.map(link => (
    <Grid item key={link.link_id}>
      <LinkCard
        data={link}
        type={CARD_TYPE.REGULAR}
        dispatch={dispatch}
      />
    </Grid>
  ))
  
  const size = useWindowSize()
    
  return (
    <>
      {props.showHeader === undefined ? (
          <Container className={classes.folderNameHeader}>
            <Typography variant="h6" gutterBottom>{name}</Typography>
          </Container>
        ) : ( "" )
      }
      <div className={classes.root} >
        <StackGrid 
          monitorImagesLoaded={true}
          columnWidth={size.width <= 320 ? size.width-16 : 160}
          gutterWidth={8}
          gutterHeight={8}
        >
          {links}
        </StackGrid>
        <Notifier 
          dispatch={dispatch}
          open={state.openSnackbar}
          message={state.snackbarMessage}
          undoButton={state.snackbarUndoButton}
        />
      </div>
      <FormDialog mode={"link"} dispatch={dispatch} defaultFolder={id}/>
      <EditFormDialog mode={"link"} from={"folder"} dispatch={dispatch}
        edit={state.showForm} {...state.clickedLinkData} />
    </>
  )
}

function useWindowSize() {
  // Initialize state with undefined width/height so server and client renders match
  // Learn more here: https://joshwcomeau.com/react/the-perils-of-rehydration/
  const [windowSize, setWindowSize] = useState({
      width: undefined,
      height: undefined,
  })

  useEffect(() => {
      // Handler to call on window resize
      function handleResize() {
      // Set window width/height to state
          setWindowSize({
              width: window.innerWidth,
              height: window.innerHeight,
          })
      }
      // Add event listener
      window.addEventListener("resize", handleResize)
      // Call handler right away so state gets updated with initial window size
      handleResize()
      // Remove event listener on cleanup
      return () => window.removeEventListener("resize", handleResize)
  }, []) // Empty array ensures that effect is only run on mount

  return windowSize
}

function useDetails(id, fetchAll) {

	const [linkDetails, setLinkDetails] = useState([])

	useEffect(() => {
    const linkRef = firebase.firestore().collection("links")
    if(fetchAll === true) {
      linkRef.orderBy("date_added", "desc").onSnapshot((snapshot) => {
				const newDetails = snapshot.docs.map((doc) => ({
					link_id: doc.id,
					...doc.data()
				}))
				setLinkDetails(newDetails)
			})
    } else {
      linkRef.where("folder_id", "==", id)
      .onSnapshot(snapshot => {
        const newDetails = snapshot.docs.map((doc) => ({
          link_id: doc.id,
          ...doc.data()
        }))
        setLinkDetails(newDetails)
      })
    }
    return () => linkRef
  }, [id, fetchAll])

  // TO-DO: CLEANUP FUNCTION
  
  console.log("Function useDetails() in Folder.js called")
	
	return linkDetails
}

export default Folder